@use 'base/variables' as  v;
@use 'base/mixins' as  m;

.sobremi {
    &__texto {
        font-weight: 400;
        line-height: 1.2;
    }
    &__link {
        color: v.$primario;
        font-weight: 700;
        font-size: 1.8rem;
    }
    &__conocimiento,
    &__versiones {
        padding-left: 2rem;
    }
    &__opciones {
        margin-bottom: 2rem;
        &:last-of-type {
            margin-bottom: 0;
        }
        &:nth-child(1) {
            &::before {
                @include m.bgImagePe("../img/logos/html.png");
            }
        }
        &:nth-child(2) {
            &::before {
                @include m.bgImagePe("../img/logos/css.png");
            }
        }
        &:nth-child(3) {
            &::before {
                @include m.bgImagePe("../img/logos/js.png");
            }
        }
    }
    &__opciones-ver {
        margin-bottom: 2rem;
        &:last-of-type {
            margin-bottom: 0;
        }
    }
}
