@use 'base/variables' as  v;
@use 'base/mixins' as  m;

.servicios {
    &__grid {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: space-evenly;
        gap: 2rem;
        @include m.tablet {
            flex-direction: row;
        }
    }
    &__card {
        padding: 2rem;
        background-image: radial-gradient(circle, v.$primario 0, #8be8c9 100%);
        color: v.$secundario;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: space-around;
        gap: 2rem;
        text-align: center;
        @include m.tablet {
            width: 25%;
            height: 56rem;
        }
        cursor: pointer;
        transition-property: transform;
        transition-duration: .3s;
        &:hover {
            transform: scale(1.05);
        }
    }
    .basico {
        border-radius: 4rem 0 4rem 0;
        border: 4px solid v.$secundario;
    }
    .estandar {
        background-image: radial-gradient(circle, v.$secundario 0%, v.$gris 100%);
        color: v.$blanco;
        border-radius: 0 0 4rem 4rem;
        border: 4px solid v.$negro;
    }
    .premium {
        border-radius: 0 4rem 0 4rem;
        border: 4px solid v.$secundario;
    }
    &__img {
        width: 10rem;
        margin: 0 auto;
    }
    &__titulo {
        font-weight: 700;
    }
    &__listado {
        list-style: none;
        padding: 0;
        margin: 0;
    }
    &__texto {
        font-size: 1.8rem;
        font-weight: 400;
        padding: 0;
        margin: 0;
        margin-bottom: .5rem;
    }
    &__precio {
        font-size: 4rem;
        font-weight: 900;
    }
}