@use 'base/variables' as  v;
@use 'base/mixins' as  m;

.footer {
    background-color: v.$secundario;
    padding: 2rem;
    margin-top: 5rem;
    &__grid {
        margin-top: 2rem;
        display: flex;
        flex-direction: column;
        align-items: center;
        @include m.tablet {
            flex-direction: row;
            justify-content: space-around;
        }        
    }
    &__lista {
        display: flex;
        flex-direction: column;
        align-items: center;
    }
    &__titulo {
        color: v.$blanco;
        margin: 0;
    }
    &__link {
        color: v.$blanco;
        font-size: 1.8rem;
        padding: 1rem;
        margin-bottom: 1rem;
    }
}