@use 'base/variables' as  v;
@use 'base/mixins' as  m;

.proyectos {
    &__titulo {
        font-size: 3rem;
    }
    &__span {
        font-size: 1.2rem;
        font-weight: 400;
    }
    &__form {
        display: flex;
        flex-direction: column;
        align-items: center;
    }
    &__formulario {
        background-color: v.$grisClaro;
        padding: 2rem;
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 1rem;
        border: 2px solid v.$gris;
        @include m.tablet {
            flex-direction: row;
            justify-content: space-between;
            gap: 3rem;
        }
    }
    &__campo {
        background-color: v.$blanco;
        border: none;
        padding: 1.4rem 1rem;
        width: 18rem;
        cursor: pointer;
        &:last-of-type {
            margin-bottom: 2rem;
        }
        @include m.tablet {
            &:last-of-type {
                margin-bottom: 0rem;
            }
        }
    }
    &__boton {
        @include m.boton(v.$primario, v.$secundario);
        padding: 1.2rem 2rem;
    }

    &__grid {
        padding: 4rem 2rem;
        @include m.grid(1, 2rem);
        @include m.tablet {
            @include m.grid(2, 2rem);
        }
        @include m.desktop {
            @include m.grid(3, 2rem);
        }
    }
    &__cajas {
        background-color: v.$secundario;
        box-shadow: 0 0 2.6rem -0.8rem v.$negro;
        transition-property: transform;
        transition-duration: .3s;
        &:hover {
            transform: scale(1.05);
        }
    }
    &__imagen {
        width: 40rem;
        height: 20rem;
    }
}