.text-center {
  text-align: center;
}

.bg-primario {
  background-color: #1FA7A7;
}

.pad {
  padding: 3rem 0 2rem 0;
}

[class*=__header] {
  margin: 4rem 0 4rem 0;
}

[class*=__titulo-pri] {
  color: #FFFFFF;
  text-transform: capitalize;
  line-height: 0.8;
  color: #FFFFFF;
  font-weight: 400;
}

[class*=__span-tit] {
  color: #202020;
  font-weight: 700;
}

[class*=__span-min] {
  color: #1FA7A7;
  display: block;
  font-size: 2rem;
  font-weight: 700;
}

/** Media Queries **/
h1, h2, h3 {
  font-family: "Roboto", sans-serif;
  margin: 0 0 5rem 0;
  text-transform: uppercase;
  text-align: center;
  line-height: 1.5;
}

h1 {
  font-size: 3.5rem;
  margin: 0;
}
@media (min-width: 768px) {
  h1 {
    font-size: 4rem;
  }
}
@media (min-width: 1024px) {
  h1 {
    font-size: 4.5rem;
  }
}

h2 {
  font-size: 2.6rem;
  margin: 0.2rem;
  color: #1FA7A7;
  line-height: 1;
  text-transform: capitalize;
}
@media (min-width: 768px) {
  h2 {
    font-size: 3rem;
  }
}
@media (min-width: 1024px) {
  h2 {
    font-size: 3.5rem;
  }
}
h2 span {
  color: #313c4e;
  font-size: 1.2rem;
  display: block;
}

h3 {
  font-size: 2.6rem;
}
@media (min-width: 768px) {
  h3 {
    font-size: 2.8rem;
  }
}
@media (min-width: 1024px) {
  h3 {
    font-size: 3rem;
  }
}

html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Roboto", sans-serif;
  font-size: 1.6rem;
  user-select: none;
}

p {
  font-size: 1.6rem;
}
@media (min-width: 768px) {
  p {
    font-size: 1.8rem;
  }
}
@media (min-width: 1024px) {
  p {
    font-size: 2rem;
  }
}

.contenedor {
  width: 90%;
  margin: 0 auto;
}
@media (min-width: 480px) {
  .contenedor {
    max-width: 40rem;
  }
}
@media (min-width: 580px) {
  .contenedor {
    max-width: 50rem;
  }
}
@media (min-width: 768px) {
  .contenedor {
    max-width: 60rem;
  }
}
@media (min-width: 868px) {
  .contenedor {
    max-width: 70rem;
  }
}
@media (min-width: 1024px) {
  .contenedor {
    max-width: 100rem;
  }
}
@media (min-width: 1224px) {
  .contenedor {
    max-width: 120rem;
  }
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  display: block;
}

.header {
  background-image: linear-gradient(rgba(57, 70, 89, 0.5) 0%, rgba(57, 70, 89, 0.5) 100%), url("../img/fondo.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-repeat: no-repeat;
  background-size: 200%;
  background-position: center center, -2rem -11rem;
  padding: 4rem 4rem;
}
@media (min-width: 768px) {
  .header {
    background-position: center center, center -31rem;
    background-size: cover;
  }
}
.header__grid {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
}
@media (min-width: 768px) {
  .header__grid {
    flex-direction: row;
  }
}
.header__navegacion {
  margin-top: 2rem;
}
@media (min-width: 768px) {
  .header__navegacion {
    margin-top: 0;
  }
}
.header__nav {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  align-items: center;
  justify-content: space-evenly;
}
@media (min-width: 768px) {
  .header__nav {
    flex-direction: row;
  }
}
.header__link {
  color: #FFFFFF;
  font-size: 1.8rem;
  padding: 0.4rem 1rem;
  border-radius: 0.6rem;
  transition-property: background color;
  transition-duration: 0.3s;
}
@media (min-width: 768px) {
  .header__link {
    font-size: 2rem;
  }
}
@media (min-width: 1024px) {
  .header__link {
    font-size: 2.4rem;
  }
}
.header__link:hover {
  background-color: #1FA7A7;
  color: #313c4e;
}

.loop {
  overflow: hidden;
  position: relative;
  min-height: 42rem;
}
@media (min-width: 768px) {
  .loop {
    height: auto;
  }
}
.loop__slide {
  flex-shrink: 0;
  position: relative;
  transition-property: transform;
  background-size: 100%;
  display: grid;
  align-items: center;
  grid-template-rows: 1fr 1fr;
}
@media (min-width: 768px) {
  .loop__slide {
    grid-template-rows: unset;
    grid-template-columns: 1fr 1fr;
  }
}
.loop__contenido {
  grid-row: 2/3;
  padding: 1rem 5rem;
  text-align: center;
}
@media (min-width: 768px) {
  .loop__contenido {
    padding: 8rem 8rem;
    grid-column: 1/2;
    grid-row: unset;
  }
}
.loop__titulo {
  text-transform: none;
}
.loop__descripcion {
  margin-bottom: 4rem;
}
.loop__imagen {
  margin: 1rem 1rem;
}
@media (min-width: 768px) {
  .loop__imagen {
    margin: 8rem 8rem;
    grid-column: 2/3;
  }
}
.loop__img {
  width: 15rem;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .loop__img {
    width: 50rem;
  }
}
.loop__boton {
  background-color: #1FA7A7;
  font-size: 2rem;
  padding: 1.2rem 4rem;
  font-weight: 700;
  display: block;
  width: 100%;
  text-align: center;
  color: #313c4e;
  text-decoration: none;
  transition: background-color 0.3s ease;
  border: none;
  display: inline;
}
.loop__boton:hover {
  cursor: pointer;
  background-color: #177c7c;
}
@media (min-width: 768px) {
  .loop__boton {
    width: 30rem;
  }
}
.loop .swiper-button-next,
.loop .swiper-button-prev {
  color: #1FA7A7;
}
.loop .swiper-pagination-bullet {
  background-color: #313c4e;
  opacity: 0.5;
}
.loop .swiper-pagination-bullet-active {
  background-color: #1FA7A7;
}

.proyectos__titulo {
  font-size: 3rem;
}
.proyectos__span {
  font-size: 1.2rem;
  font-weight: 400;
}
.proyectos__form {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.proyectos__formulario {
  background-color: #ECEEF2;
  padding: 2rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1rem;
  border: 2px solid #484349;
}
@media (min-width: 768px) {
  .proyectos__formulario {
    flex-direction: row;
    justify-content: space-between;
    gap: 3rem;
  }
}
.proyectos__campo {
  background-color: #FFFFFF;
  border: none;
  padding: 1.4rem 1rem;
  width: 18rem;
  cursor: pointer;
}
.proyectos__campo:last-of-type {
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  .proyectos__campo:last-of-type {
    margin-bottom: 0rem;
  }
}
.proyectos__boton {
  background-color: #1FA7A7;
  font-size: 2rem;
  padding: 1.2rem 4rem;
  font-weight: 700;
  display: block;
  width: 100%;
  text-align: center;
  color: #313c4e;
  text-decoration: none;
  transition: background-color 0.3s ease;
  border: none;
  padding: 1.2rem 2rem;
}
.proyectos__boton:hover {
  cursor: pointer;
  background-color: #177c7c;
}
@media (min-width: 768px) {
  .proyectos__boton {
    width: 30rem;
  }
}
.proyectos__grid {
  padding: 4rem 2rem;
  display: grid;
  gap: 2rem;
  grid-template-columns: repeat(1, 1fr);
}
@media (min-width: 768px) {
  .proyectos__grid {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 1024px) {
  .proyectos__grid {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.proyectos__cajas {
  background-color: #313c4e;
  box-shadow: 0 0 2.6rem -0.8rem #202020;
  transition-property: transform;
  transition-duration: 0.3s;
}
.proyectos__cajas:hover {
  transform: scale(1.05);
}
.proyectos__imagen {
  width: 40rem;
  height: 20rem;
}

.servicios__grid {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
  gap: 2rem;
}
@media (min-width: 768px) {
  .servicios__grid {
    flex-direction: row;
  }
}
.servicios__card {
  padding: 2rem;
  background-image: radial-gradient(circle, #1FA7A7 0, #8be8c9 100%);
  color: #313c4e;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  gap: 2rem;
  text-align: center;
  cursor: pointer;
  transition-property: transform;
  transition-duration: 0.3s;
}
@media (min-width: 768px) {
  .servicios__card {
    width: 25%;
    height: 56rem;
  }
}
.servicios__card:hover {
  transform: scale(1.05);
}
.servicios .basico {
  border-radius: 4rem 0 4rem 0;
  border: 4px solid #313c4e;
}
.servicios .estandar {
  background-image: radial-gradient(circle, #313c4e 0%, #484349 100%);
  color: #FFFFFF;
  border-radius: 0 0 4rem 4rem;
  border: 4px solid #202020;
}
.servicios .premium {
  border-radius: 0 4rem 0 4rem;
  border: 4px solid #313c4e;
}
.servicios__img {
  width: 10rem;
  margin: 0 auto;
}
.servicios__titulo {
  font-weight: 700;
}
.servicios__listado {
  list-style: none;
  padding: 0;
  margin: 0;
}
.servicios__texto {
  font-size: 1.8rem;
  font-weight: 400;
  padding: 0;
  margin: 0;
  margin-bottom: 0.5rem;
}
.servicios__precio {
  font-size: 4rem;
  font-weight: 900;
}

.contacto__contenido {
  border: 2px solid #313c4e;
}
.contacto__formulario {
  background-color: #ECEEF2;
  padding: 2rem 2rem;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
}
@media (min-width: 768px) {
  .contacto__formulario {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
  }
}
.contacto__campo {
  background-color: #ECEEF2;
  border: 3px solid #FFFFFF;
}
.contacto__campo:nth-child(3) {
  display: block;
  height: 30rem;
  grid-column: 1/3;
}
.contacto__subtitulo {
  padding: 0 0.5rem;
  color: #313c4e;
  font-size: 2rem;
  font-weight: 700;
}
.contacto__input {
  background-color: #ECEEF2;
  border: none;
  display: block;
  width: 100%;
}
.contacto textarea {
  background-color: #ECEEF2;
  border: none;
  display: block;
  resize: none;
  width: 100%;
  height: 100%;
}
.contacto__boton {
  grid-column: 1/3;
  background-color: #1FA7A7;
  font-size: 2rem;
  padding: 1.2rem 4rem;
  font-weight: 700;
  display: block;
  width: 100%;
  text-align: center;
  color: #313c4e;
  text-decoration: none;
  transition: background-color 0.3s ease;
  border: none;
  margin: 0 auto;
}
.contacto__boton:hover {
  cursor: pointer;
  background-color: #177c7c;
}
@media (min-width: 768px) {
  .contacto__boton {
    width: 30rem;
  }
}
.contacto__imagen {
  grid-column: 1/3;
  width: 20rem;
  margin: 0 auto;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-around;
  gap: 2rem;
}
.contacto__img {
  width: 3rem;
  height: 3rem;
}

