  padding: 2rem;
  margin-top: 5rem;
}
.footer__grid {
  margin-top: 2rem;
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .footer__grid {
    flex-direction: row;
    justify-content: space-around;
  }
}
.footer__lista {
