@use 'base/variables' as  v;
@use 'base/mixins' as  m;

.loop {
    overflow: hidden;
    position: relative;
    min-height: 42rem;
    @include m.tablet {
        height: auto;
    }
    &__slide {
        flex-shrink: 0;
        position: relative;
        transition-property: transform;
        background-size: 100%;
        display: grid;
        align-items: center;
        grid-template-rows: 1fr 1fr;
        @include m.tablet {
            grid-template-rows: unset;
            grid-template-columns: 1fr 1fr;
        }
    }
    &__contenido {
        grid-row: 2 / 3 ;
        padding: 1rem 5rem;
        text-align: center;
        @include m.tablet {
            padding: 8rem 8rem;
            grid-column: 1 / 2 ;
            grid-row: unset;
        }
    }
    &__titulo {
        text-transform: none;
    }
    &__descripcion {
        margin-bottom: 4rem;
    }
    &__imagen {
        margin: 1rem 1rem;
        @include m.tablet {
            margin: 8rem 8rem;
            grid-column: 2 / 3;
        }
    }
    &__img {
        width: 15rem;
        margin: 0 auto;
        @include m.tablet {
            width: 50rem;
        }
    }
    &__boton {
        @include m.boton(v.$primario, v.$secundario);
        display: inline;
        
    }
    .swiper-button-next,
    .swiper-button-prev {
        color: v.$primario;
    }
    .swiper-pagination-bullet {
        background-color: v.$secundario;
        opacity: .5;
    }
    .swiper-pagination-bullet-active {
        background-color: v.$primario;
    }
}