@use 'base/variables' as  v;
@use 'base/mixins' as  m;

.header {
    @include m.bgImage("../img/fondo.jpg", v.$secundarioOpacity);
    background-repeat: no-repeat;
    background-size: 200%;
    background-position: center center, -2rem -11rem;
    @include m.tablet {
        background-position: center center, center -31rem;
        background-size: cover;
    }
    padding: 4rem 4rem;
    &__grid {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: space-between;
        @include m.tablet {
            flex-direction: row;
        }
    }

    &__navegacion {
        margin-top: 2rem;
        @include m.tablet {
            margin-top: 0;
        }
    }
    &__nav {
        display: flex;
        flex-direction: column;
        gap: 1rem;
        align-items: center;
        justify-content: space-evenly;
        @include m.tablet {
            flex-direction: row;
        }
    }
    &__link {
        color: v.$blanco;
        font-size: 1.8rem;
        padding: .4rem 1rem;
        border-radius: .6rem;
        transition-property: background color;
        transition-duration: .3s;
        @include m.tablet {
            font-size: 2rem;
        }
        @include m.desktop {
            font-size: 2.4rem;
        }
        &:hover {
            background-color: v.$primario;
            color: v.$secundario;
        }
    }
}