@use 'base/variables' as  v;
@use 'base/mixins' as  m;

.contacto {
    &__contenido {
        border: 2px solid v.$secundario;
    }
    &__formulario {
        background-color: v.$grisClaro;
        padding: 2rem 2rem;
        display: flex;
        flex-direction: column;
        justify-content: space-around;
        align-items: center;
        @include m.tablet {
            display: grid;
            grid-template-columns: repeat(2, 1fr);
            gap: 2rem;
        }
    }
    &__campo {
        background-color: v.$grisClaro;
        border: 3px solid v.$blanco;
        &:nth-child(3) {
            display: block;
            height: 30rem;
            grid-column: 1 / 3;
        }
    }
    &__subtitulo {
        padding: 0 .5rem;
        color: v.$secundario;
        font-size: 2rem;
        font-weight: 700;
    }
    &__input {
        background-color: v.$grisClaro;
        border: none;
        display: block;
        width: 100%;
    }
    textarea {
        background-color: v.$grisClaro;
        border: none;
        display: block;
        resize:none;
        width: 100%;
        height: 100%;
    }
    &__boton {
        grid-column: 1 / 3;
        @include m.boton(v.$primario, v.$secundario);
        margin: 0 auto;
    }
    &__imagen {
        grid-column: 1 / 3;
        width: 20rem;
        margin: 0 auto;
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: space-around;
        gap: 2rem;
    }
    &__img {
        width: 3rem;
        height: 3rem;
    }
}